import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Panel } from "primereact/panel";
import { Button } from "primereact/button";

function CursosPage() {
  const [cursos, setCursos] = useState([]);
  const [nuevoCurso, setNuevoCurso] = useState("");
  const [editandoId, setEditandoId] = useState(null);
  const [nombreEditado, setNombreEditado] = useState("");

  const API = process.env.REACT_APP_API_URL;
  const navigate = useNavigate();

  const obtenerCursos = async () => {
    const res = await fetch(`${API}/cursos`);
    const data = await res.json();
    setCursos(data);
  };

  const crearCurso = async () => {
    if (!nuevoCurso.trim()) return;

    await fetch(`${API}/cursos`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ nombre: nuevoCurso }),
    });

    setNuevoCurso("");
    obtenerCursos();
  };

  const eliminarCurso = async (id) => {
    if (!window.confirm("¿Eliminar este curso?")) return;

    await fetch(`${API}/cursos/${id}`, {
      method: "DELETE",
    });

    obtenerCursos();
  };

  const iniciarEdicion = (curso) => {
    setEditandoId(curso.id);
    setNombreEditado(curso.nombre);
  };

  const guardarEdicion = async (id) => {
    await fetch(`${API}/cursos/${id}`, {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ nombre: nombreEditado }),
    });

    setEditandoId(null);
    setNombreEditado("");
    obtenerCursos();
  };

  useEffect(() => {
    obtenerCursos();
  }, []);

  return (
    <div className="min-h-screen bg-gray-100 p-4">
      <div className="max-w-xl mx-auto">
        <Panel header="Gestión de Cursos" className="mb-4">
          <div className="mb-3 flex gap-2">
            <input
              type="text"
              placeholder="Nuevo curso"
              className="p-inputtext p-component flex-1"
              value={nuevoCurso}
              onChange={(e) => setNuevoCurso(e.target.value)}
            />
            <Button label="Agregar" icon="pi pi-plus" onClick={crearCurso} />
          </div>

          <ul className="space-y-2">
            {cursos.map((curso) => (
              <li
                key={curso.id}
                className="flex justify-between items-center bg-white p-2 rounded shadow"
              >
                {editandoId === curso.id ? (
                  <input
                    className="border px-2 py-1 rounded flex-1 mr-2"
                    value={nombreEditado}
                    onChange={(e) => setNombreEditado(e.target.value)}
                  />
                ) : (
                  <span className="flex-1">{curso.nombre}</span>
                )}
                <div className="flex gap-2 ml-2">
                  {editandoId === curso.id ? (
                    <Button
                      icon="pi pi-check"
                      className="p-button-success p-button-sm"
                      onClick={() => guardarEdicion(curso.id)}
                    />
                  ) : (
                    <Button
                      icon="pi pi-pencil"
                      className="p-button-info p-button-sm"
                      onClick={() => iniciarEdicion(curso)}
                    />
                  )}
                  <Button
                    icon="pi pi-trash"
                    className="p-button-danger p-button-sm"
                    onClick={() => eliminarCurso(curso.id)}
                  />
                </div>
              </li>
            ))}
          </ul>
        </Panel>

        <Button
          label="Volver al Inicio"
          icon="pi pi-home"
          className="p-button-secondary w-full"
          onClick={() => navigate("/")}
        />
      </div>
    </div>
  );
}

export default CursosPage;
